body {
  color: #e2e8f0;
  background-color: #27272a;
}

h1, p {
  margin: 0;
  padding: 0;
}

span {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.75rem;
}

.loader, .error {
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-size: 1.5rem;
  line-height: 2rem;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader-spin {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  margin-top: 5.5rem;
  display: flex;
}

.cat-info-title {
  border-bottom: 2px solid #e2e8f0;
  font-size: 2.25rem;
  line-height: 2.5rem;
}

.cat-info-desc {
  max-width: 900px;
  min-height: 40px;
  font-size: 1.125rem;
  line-height: 1.75rem;
}

.cat-info-temperament {
  max-width: 900px;
}

.cat-info-img {
  width: 500px;
  height: 300px;
  border-radius: 1rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, .1), 0 8px 10px -6px rgba(0, 0, 0, .1);
}

